import React, { useState, useEffect } from 'react';
import { Route, Switch } from "react-router-dom";
import Footer from './components/Footer';
import Header from './components/Header';
import TopPage from './components/TopPage';
import Aboutus from './components/Aboutus';
import Contact from './components/Contact';
import Library from './components/Library';
import Works from './components/Works';
import LibraryIcon from './components/Library-icon';
import './App.css';

function App(){
  const [pageNumber, setPageNumber] = useState(0);

  const checkPage = () => {
    if(window.location.hash === '#/'){
      setPageNumber(0);
    } else if(window.location.hash === '#/Aboutus'){
      setPageNumber(1);
    } else if(window.location.hash === '#/Library' | window.location.hash === '#/Icons' | window.location.hash === '#/Others'){
      setPageNumber(2);
    } else if(window.location.hash === '#/Contact'){
      setPageNumber(3);
    } else if(window.location.hash === '#/Works')  {
      setPageNumber(4);
    }
  }

  useEffect(() => {
    checkPage();
    window.onpopstate = checkPage;
  },[])

  const libraryUrl = {
    icons: [
      "ApplicationsFolderIcon.png",
      "DownloadsFolder.png",
      "Drive.png",
      "Folder-AD.png",
      "Folder-Ae.png",
      "Folder-Ai.png",
      "Folder-Au.png",
      "Folder-Dw.png",
      "Folder-Fl.png",
      "Folder-Gw.png",
      "Folder-Id.png",
      "Folder-Ps.png",
      "GenericAirDiskIcon.png",
      "GenericFolderIcon.png",
      "GenericTimeMachineDiskIcon.png",
      "LibraryFolderIcon.png",
      "MusicFolderIcon.png",
      "SidebarRemovableDisk.png",
      "SystemFolderIcon.png",
      "UsersFolderIcon.png"
    ],
    others: [
      "Popicons1.png",
      "Popicons2.png",
      "Popicons3.png",
      "Popicons4.png",
      "Popicons5.png",
      "Popicons6.png",
      "Popicons7.png",
      "Popicons8.png",
      "Popicons9.png",
      "Popicons10.png",
      "Popicons11.png",
      "Popicons12.png"
    ]
  }

  const libraryFolders = ["Mac Folder Icons","Pop icons"];

  return(
    <>
        <Header
            pageNumber = {pageNumber}
            setPageNumber0 = {() => setPageNumber(0)}
            setPageNumber1 = {() => setPageNumber(1)}
            setPageNumber2 = {() => setPageNumber(2)}
            setPageNumber3 = {() => setPageNumber(3)}
            setPageNumber4 = {() => setPageNumber(4)}
          />
        <div className="mainContents">
          <Switch>
            <Route exact path="/" render={() => <TopPage
                setPageNumber1 = {() => setPageNumber(1)}
                setPageNumber2 = {() => setPageNumber(2)}
                setPageNumber3 = {() => setPageNumber(3)}
                setPageNumber4 = {() => setPageNumber(4)}
              /> } />
            <Route path="/Aboutus" component={Aboutus} />
            <Route path="/Library" render={() => <Library
                pageNumber = {pageNumber}
                setPageNumber2 = {() => setPageNumber(2)}
              />} />
            <Route path="/Contact" component={Contact} />
            <Route path="/Works" render={() => <Works
                setPageNumber0 = {() => setPageNumber(0)}
              />} />
              <Route path="/Icons" render={() => <LibraryIcon
                url = {libraryUrl.icons}
                folder = {libraryFolders[0]}
              />} />
              <Route path="/Others" render={() => <LibraryIcon
                url = {libraryUrl.others}
                folder = {libraryFolders[1]}
              />} />
          </Switch>
        </div>
        <Footer
          pageNumber = {pageNumber}
        />
    </>
  )
}

export default App;