import React,{ useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './css/Library.css';
import LibraryIcon from './Library-icon';

function LibraryTopImage() {
  return(
    <>
      <div className="top-image library">
        <img src={`${process.env.PUBLIC_URL}/img/illust-library.png`} />
      </div>
    </>
  )
}

function LibraryContent(props) {
  const [clicked, setClicked] = useState('');

  const contentImageItems = props.contentImages.map((item, index) => 
    <>
      <li key={item.toString()} className={clicked === index ? 'library-content-item active' : 'library-content-item'}>
        <img src={`${process.env.PUBLIC_URL}/img/${item}`} onClick={clicked === '' ? () => setClicked(index) : () => setClicked('')} />
      </li>
    </>
  )

  return(
    <>
      <div className="library-content">
        <div className="library-content-top">
          <img src={props.topImageUrl} />
          <h1>{props.text}</h1>
        </div>
        <ul className="library-content-images">
          {contentImageItems}
        </ul>
        <div className={clicked === '' ? 'mask' : 'mask active'}></div>
      </div>
    </>
  )
}

function LibraryVideos(props) {

  const videos = [
    "Metal Ring.mov",
    "Discover.mov",
    "Small Space.mov"
  ];
  const url = [
    "https://www.youtube.com/watch?v=41XTNGMnwAY",
    "https://www.youtube.com/watch?v=TeVbqg0RkXQ",
    "https://www.youtube.com/watch?v=8CJPqiT8rVA"
  ];

  const videoItems = videos.map((item, index) => 
    <a href={url[index]} key={item.toString()}>
      <video src={`${process.env.PUBLIC_URL}/video/${item}`} id={`libraryVideo${index}`} playsInline muted onMouseOver={() => {document.getElementById(`libraryVideo${index}`).play()}} onMouseLeave={() => {document.getElementById(`libraryVideo${index}`).pause()}} />
    </a>
  )

  return(
    <>
      <div className="library-content-top">
        <img src={`${process.env.PUBLIC_URL}/img/library-img-icon-animation.png`} />
        <h1>アニメーション</h1>
      </div>
      <div className="library-content-videos">
        {videoItems}
      </div>
    </>
  )
}

function LibraryOthers(props) {
  return(
    <>
      <div className="library-content-top">
        <img src={`${process.env.PUBLIC_URL}/img/library-img-icon-design.png`} />
        <h1>その他アイコン等</h1>
      </div>
      <ul className="library-content-images">
        <li>
          <Link to="/Icons">
            <img src={`${process.env.PUBLIC_URL}/img/library-img-other-003.jpg`} />
          </Link>
        </li>
        <li>
          <Link to="/Others">
            <img src={`${process.env.PUBLIC_URL}/img/library-img-other-004.jpg`} />
          </Link>
        </li>
      </ul>
    </>
  )
}

function LibraryContents(props) {
  const topImageUrl = [
    "library-img-icon-chirashi.png",
    "library-img-icon-design.png"
  ];
  const text = [
    "ちらし・広告・パンフレット",
    "ロゴ・イラスト等"
  ];
  const contentImages = {
    flyers: [
      "library-img-chirashi-003-crop.jpg",
      "library-img-chirashi-001-crop.jpg",
      "library-img-chirashi-002-crop.jpg"
    ],
    rogos: [
      "library-img-other-001.jpg",
      "library-img-other-002.jpg",
      "Nenga2015-moza.jpg",
      "Nenga2016_moza.jpg",
      "Nenga2017_moza.jpg",
      "Nenga2018_moza.jpg",
      "Nenga2019-mercari_moza.jpg",
      "Nenga2020_moza.jpg"
    ]
  }
  
  return(
    <>
      <LibraryContent
        topImageUrl = {`${process.env.PUBLIC_URL}/img/${topImageUrl[0]}`}
        text = {text[0]}
        contentImages = {contentImages.flyers}
      />
      <LibraryVideos />
      <LibraryContent
        topImageUrl = {`${process.env.PUBLIC_URL}/img/${topImageUrl[1]}`}
        text = {text[1]}
        contentImages = {contentImages.rogos}
      />
      <LibraryOthers />
    </>
  )
}

function Library(props) {
  return(
    <>
      <LibraryTopImage />
      <LibraryContents />
    </>
  )
}

export default Library;